<!--
 * @Descripttion: 基础数据维护列表
 * @version: 
 * @Author: 曾元球
 * @Date: 2019-07-01 09:28:15
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-12 14:52:08
 -->
<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">基础数据维护</div>
      <div class="head-btn"
        v-if="btns.hasInsert"
        data-btn-id='106006001'
        @click="openDialog('insert')">
        <img src="@/assets/img/add.png"
          alt="登记">&nbsp;<span>新增</span>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        size='small'
        class="default-form-style">
        <el-form-item label='小项名称：'>
          <el-input placeholder="请输入小项名称"
            v-model="querys.pname"
            clearable></el-input>
        </el-form-item>

        <el-form-item label='项目名称：'>
          <el-input placeholder="请输入项目名称"
            v-model="querys.parentName"
            clearable></el-input>
        </el-form-item>
        <el-form-item label='所属机构名称：'>
          <el-select v-model="querys.createOrganizationId"
            placeholder="全部"
            clearable>
            <el-option v-for="item in orgList"
              :key='item.sysGuid'
              :value="item.sysGuid"
              :label="item.oname" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search"
            round
            @click="$refs.pagination.initPageList(1)">查询</el-button>
        </el-form-item>

        <el-form-item label="显示状态："
          style="float:right;margin-right:0">
          <el-radio-group class="red"
            v-model="querys.enabled"
            @change="$refs.pagination.initPageList(1)">
            <el-radio label="">全部</el-radio>
            <el-radio label="1">已启用</el-radio>
            <el-radio label="2">已禁用</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-box flex-item">
      <el-table :data='tableData'
        height='100%'
        header-cell-class-name='custom-table-cell'
        v-loading='loading'
        stripe>
        <el-table-column label='序号'
          type='index'>
          <template v-slot='{$index}'>
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column label='机构名称'
          prop="oname"
          show-overflow-tooltip></el-table-column>
        <el-table-column label='小项顺序'
          prop='sortNum'
          width="84"></el-table-column>
        <el-table-column label='小项编号'
          prop='sysGuid'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='小项名称'
          prop='pname'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='小项状态'
          width="84">
          <template v-slot="{row}">
            <span :class="[row.enabled == 1 ? 'status-enable' : 'status-disable']"></span>
          </template>
        </el-table-column>
        <el-table-column label='结果类型'
          prop='resultType'
          width='84'>
          <template v-slot="{row}">
            {{row.resultType == 1 ? '数值型' : '文本型'}}
          </template>
        </el-table-column>

        <!-- v-if='btns.hasSeeRule' 没有查看权限不用显示这一列 -->
        <el-table-column label='项目规则'
          v-if="btns.hasSeeRule"
          width="100">
          <template v-slot="{row}">
            <a class="link"
              @click="jumpRuleDetail(row)">
              查看规则
            </a>
          </template>
        </el-table-column>
        <el-table-column label='项目名称'
          prop='parentName'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='归属科室'
          prop='deptName'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='创建日期'
          width="100">
          <template v-slot="{row}">
            {{row.createDate | formatDate}}
          </template>
        </el-table-column>
        <el-table-column label='操作'>
          <template v-slot="{row}">
            <i class="iconfont bj-search"
              @click="openDialog('see', row)"></i>
            <i class="iconfont bj-edit"
              v-if="btns.hasUpdate&&row.enabled==1"
              data-btn-id='106006002'
              @click="openDialog('update', row)"></i>
            <i class="iconfont bj-delete"
              v-if="btns.hasDel&&row.enabled==1"
              data-btn-id='106006003'
              @click="delConfirm(row)"></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="foot">
      <bj-pagination queryUrl='/sys/project/listPaging'
        :queryObj='querys'
        @listResult='getListResult'
        @controlLoading='loading=$event'
        ref='pagination' />
    </div>

    <el-dialog :visible.sync="dialogIsShow"
      custom-class='my-dialog'
      class="flex"
      width='960px'
      :title="dialogTitles[dialogType]"
      :close-on-click-modal='false'
      center>
      <template v-if='dialogType==="see"'>
        <el-form inline
          size="small"
          label-width="auto"
          label-position="left"
          style="margin: 0 120px"
          key='1'>
          <el-row>
            <el-col :span="8">
              <el-form-item label="科室名称：">
                {{dataInfo.deptName}}
              </el-form-item>
            </el-col>

            <el-col :span="16">
              <el-form-item label="科室编号：">
                {{dataInfo.deptId}}
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="项目名称：">
                {{dataInfo.parentName}}
              </el-form-item>
            </el-col>

            <el-col :span="16">
              <el-form-item label="项目编号：">
                {{dataInfo.parentId}}
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="小项名称：">
                {{dataInfo.pname}}
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="小项顺序：">
                {{dataInfo.sortNum}}
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="项目单位：">
                {{dataInfo.projectSymbol}}
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="拼音简码：">
                {{dataInfo.pySimpleCode}}
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="五笔简码：">
                {{dataInfo.wbSimpleCode | emptyData}}
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="结果类型：">
                {{
                  dataInfo.resultType == 1 ? '数值型' : 
                  dataInfo.resultType == 2 ? '文本型(医技检查)' :
                  '文本型(科室检查)'
                }}
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="默认结果：">
                {{dataInfo.defaultValue}}
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="仪器名称：">
                {{dataInfo.machineName}}
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="仪器编码：">
                {{dataInfo.machineCode}}
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="是否启用：">
                {{dataInfo.enabled == 1 ? '是' : '否'}}
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="性别：">
                {{dataInfo.gender == 1 ? '共用' : dataInfo.gender == 2 ? '男性' : '女性'}}
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <div slot='footer'
          v-if="btns.hasUpdate"
          data-btn-id='106006002'>
          <el-button class="btn-info"
            @click="openDialog('update')">修改</el-button>
        </div>
      </template>
      <template v-else>
        <el-form inline
          size="small"
          label-width="auto"
          class="default-form-style"
          :model="formFields"
          :rules="rules"
          ref='form'
          key='2'>
          <el-row>
            <el-col :span="8">
              <el-form-item label="科室名称："
                prop="deptId">
                <el-select v-model="formFields.deptId"
                  value-key="deptId"
                  @change='onDeptChange'>
                  <el-option v-for="item in deptList"
                    :key='item.sysGuid'
                    :value="{deptId: item.sysGuid, deptName: item.dname}"
                    :label="item.dname" />
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="16">
              <el-form-item label="科室编号：">
                <el-input :value="formFields.deptId.deptId"
                  readonly />
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="项目名称："
                prop="parentId">
                <el-select v-model="formFields.parentId"
                  value-key="parentId">
                  <el-option v-for="item in maxProjectList"
                    :key="item.sysGuid"
                    :value="{parentId: item.sysGuid, parentName: item.pname, resultType: item.resultType}"
                    :label="item.pname" />
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="16">
              <el-form-item label="项目编号：">
                <el-input :value="formFields.parentId.parentId"
                  readonly />
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="小项名称："
                prop="pname">
                <el-input v-model="formFields.pname"
                  placeholder="请输入"
                  maxlength="51"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="小项顺序："
                prop="sortNum">
                <el-input placeholder="请输入"
                  maxlength="5"
                  :value="formFields.sortNum"
                  @input='$outInt($event, "sortNum", formFields)' />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="项目单位："
                prop="projectSymbol">
                <el-input v-model="formFields.projectSymbol"
                  placeholder="请输入" />
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="拼音简码："
                prop="pySimpleCode">
                <el-input v-model="formFields.pySimpleCode"
                  placeholder="请输入"
                  maxlength="51" />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="五笔简码："
                prop="wbSimpleCode">
                <el-input v-model="formFields.wbSimpleCode"
                  placeholder="请输入"
                  maxlength="50" />
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="结果类型：">
                <el-input :value="resultType2Str"
                  readonly />
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="默认结果："
                prop="defaultValue">
                <el-input v-model="formFields.defaultValue"
                  placeholder="请输入" />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="仪器名称："
                prop="machineName">
                <el-input v-model="formFields.machineName"
                  placeholder="请输入" />
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="仪器编码："
                prop="machineCode">
                <el-input v-model="formFields.machineCode"
                  placeholder="请输入" />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="是否启用："
                prop="enabled">
                <el-select v-model="formFields.enabled"
                  class="block"
                  placeholder="启用">
                  <el-option value="1"
                    label="启用"></el-option>
                  <el-option value="2"
                    label="禁用"></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="性别："
                prop="gender">
                <el-select v-model="formFields.gender">
                  <el-option value="1"
                    label="共用"></el-option>
                  <el-option value="2"
                    label="男"></el-option>
                  <el-option value="3"
                    label="女"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <div slot='footer'>
          <el-button class="btn-hollowing"
            @click="dialogIsShow = false">取消</el-button>
          <el-button class="btn-info"
            @click="onSubmit"
            :loading="submitLoading">保存</el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { lenRule } from '@/lib/utils'
import { mapState } from 'vuex'
const MIN_PROJECT = '2' // 维护小项
const USE = '1'
export default {
  name: 'BasicDataList',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      tableData: [],
      querys: {
        pname: '',
        enabled: '',
        parentName: '',
        createOrganizationId,
        projectType: MIN_PROJECT
      },
      pageNum: 1,
      pageSize: 10,

      formFields: {
        deptId: '',
        // deptName: '',
        projectType: '', // string 是 项目类型1大项目, 2小项目 1
        parentId: '', // string 是 父项目ID
        // parentName: '',       // string 是 父项目名称
        sortNum: '', // string 是 排序 1
        pname: '', // string 是 项目名称 眼科2D扫码
        pySimpleCode: '', // string 是 拼音简码yk2dsm
        wbSimpleCode: '', // string 是 五笔简码yk2dsm
        projectSymbol: '', // string 是 项目单位(%,cm)
        resultType: '', // string 是 结果类型 1数值2文本 2
        defaultValue: '', // string 是 默认结果
        machineName: '', // string 是 机器名称
        machineCode: '', // string 是 机器编码
        gender: '', // string 是 1所有人可做2男性3女性1
        enabled: USE, // string 是 是否启动1开启2关闭
        projectType: MIN_PROJECT
      },

      rules: {
        deptId: { type: 'object', required: true, message: '请选择所属科室' },
        parentId: { type: 'object', required: true, message: '请选择项目名称' },
        pname: [
          {
            required: true,
            message: '请输入小项名称',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule()
        ],
        sortNum: [{ required: true, message: '请输入小项顺序', trigger: 'blur' }],
        pySimpleCode: [
          {
            required: true,
            message: '请输入拼音简码',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule()
        ],
        machineName: [
          {
            required: true,
            message: '请输入仪器名称',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule()
        ],
        machineCode: [
          {
            required: true,
            message: '请输入仪器编码',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule()
        ],
        enabled: { required: true, message: '请选择是否启用' },
        gender: { required: true, message: '请选择性别' }
      },

      dataInfo: {},
      maxProjectList: [],
      deptList: [],

      dialogIsShow: false,
      dialogType: '',
      loading: true,
      submitLoading: false
    }
  },

  computed: {
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasInsert: ~btnIds.indexOf('106006001'), // 新增按钮权限
        hasUpdate: ~btnIds.indexOf('106006002'), // 修改按钮权限
        hasDel: ~btnIds.indexOf('106006003'), // 删除按钮权限
        hasStatus: ~btnIds.indexOf('106006004'), // 更改状态权限
        hasSeeRule: ~btnIds.indexOf('106006005') // 查看规则权限
      }
    },
    resultType2Str() {
      let o = this.formFields.parentId
      if (!o) return ''
      return ['数值型', '文本型(医技检查)', '文本型(科室检查)'][o.resultType - 1]
    },
    ...mapState(['orgList'])
  },

  created() {
    this.dialogTitles = { see: '查看', insert: '新增', update: '修改' }
  },

  methods: {
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },
    delConfirm({ sysGuid }) {
      this.$confirm('', {
        title: '确定删除记录？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(e => {
          this.$post('/sys/project/remove', { sysGuid })
            .then(res => {
              this.$message.success('删除成功')
              if (this.tableData.length === 1) {
                let pageNum = this.pageNum > 1 ? this.pageNum-- : this.pageNum
                this.$refs.pagination.initPageList(pageNum)
              } else {
                this.$refs.pagination.initPageList()
              }
            })
            .catch(err => {
              console.log(err)
              this.$alert(`原因：${err}`, '删除失败', {
                showClose: false,
                customClass: 'msg-box danger'
              })
            })
        })
        .catch(cancel => cancel)
    },

    openDialog(type, data) {
      this.getAllDept()
      this.dialogIsShow = true
      this.dialogType = type
      if (data) {
        this.dataInfo = data
        Object.keys(this.formFields).forEach(key => {
          if (key === 'deptId' || key === 'parentId') return
          this.formFields[key] = data[key]
        })
        let { sysGuid, deptId, deptName, parentId, parentName, resultType } = data
        this.formFields.sysGuid = sysGuid
        this.formFields.deptId = { deptId, deptName }
        this.formFields.parentId = { parentId, parentName, resultType }
        this.getAllMaxProject(deptId)
      } else if (type === 'insert') {
        // 新增手动清空表单数据
        this.maxProjectList = []
        delete this.formFields.sysGuid
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = ''
        })
        this.formFields.enabled = USE
        this.formFields.projectType = MIN_PROJECT
      }
      this.dialogType !== 'see' && this.$nextTick(() => this.$refs.form.clearValidate())
    },

    onSubmit() {
      this.$refs.form.validate(valid => {
        if (!valid) return
        let url = `/sys/project/${this.dialogType}`
        let msg = this.dialogTitles[this.dialogType]
        let fields = this.handleFields(this.formFields)
        this.submitLoading = true
        this.$post(url, fields)
          .then(res => {
            this.submitLoading = false
            this.dialogIsShow = false
            this.$refs.pagination.initPageList()
            this.$message.success(`${msg}成功`)
          })
          .catch(err => {
            this.submitLoading = false
            this.$message.error(err)
          })
      })
    },

    handleFields(data) {
      let { deptId, parentId } = data
      return { ...data, ...deptId, ...parentId }
    },

    // 跳转到规则详情
    jumpRuleDetail({ sysGuid, resultType, pname }) {
      this.$router.push({
        name: 'RuleList',
        params: { id: `${sysGuid}&${resultType}&${pname}` }
      })
    },

    onDeptChange({ deptId }) {
      this.formFields.parentId = ''
      this.$nextTick(() => this.$refs.form.clearValidate('parentId'))
      this.getAllMaxProject(deptId)
    },

    getAllMaxProject(deptId) {
      const MAX_PROJECT = '1'
      this.$post('/sys/project/listPaging', {
        deptId,
        pageNum: '1',
        pageSize: '10000',
        enabled: USE,
        projectType: MAX_PROJECT
      }).then(({ data }) => {
        if (!data) return
        this.maxProjectList = data.list
      })
    },

    getAllDept() {
      if (this.deptList.length) return
      this.$post('/sys/dept/listPaging', {
        pageNum: '1',
        pageSize: '10000',
        enabled: USE,
        organizationGuid: this.$store.getters.o_userinfo.orgGuid
      }).then(({ data }) => {
        if (!data) return
        this.deptList = data.list
      })
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}
</style>



